.residents-section {
  padding: 100px 0;
  background: #0a0a12;
  position: relative;
}

.residents-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
}

.section-subtitle {
  text-align: center;
  color: #b3b3b3;
  font-size: 1.1rem;
  margin-bottom: 80px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 300;
  line-height: 1.6;
}

.residents-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 50px;
  max-width: 1400px;
  margin: 0 auto;
}

.resident-card {
  background: #1a1a24;
  border: 1px solid #2a2a35;
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.resident-card:hover {
  transform: translateY(-5px);
  border-color: #ff073a;
  box-shadow: 0 12px 40px rgba(255, 7, 58, 0.15);
}

.resident-image {
  position: relative;
  height: 280px;
  background: #2a2a35;
  overflow: hidden;
  border-bottom: 1px solid #2a2a35;
}

.resident-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.resident-card:hover .resident-photo {
  transform: scale(1.05);
}

.image-placeholder {
  display: none;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2a2a35, #1a1a24);
  align-items: center;
  justify-content: center;
}

.image-placeholder i {
  font-size: 3rem;
  color: #ff073a;
  opacity: 0.5;
}

.resident-overlay {
  position: absolute;
  top: 20px;
  right: 20px;
}

.resident-years {
  background: rgba(255, 7, 58, 0.9);
  color: white;
  padding: 8px 12px;
  border-radius: 8px;
  text-align: center;
  font-weight: 700;
  backdrop-filter: blur(10px);
}

.resident-years span {
  display: block;
  font-size: 1.2rem;
  line-height: 1;
}

.resident-years small {
  font-size: 0.7rem;
  opacity: 0.9;
  font-weight: 500;
}

.resident-content {
  padding: 30px;
}

.resident-header {
  margin-bottom: 20px;
  text-align: center;
}

.resident-name {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 8px;
  letter-spacing: 0.5px;
}

.resident-genre {
  color: #ff073a;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

.resident-stats {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid #2a2a35;
}

.stat {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #b3b3b3;
  font-size: 0.9rem;
}

.stat i {
  color: #ff073a;
  font-size: 0.8rem;
}

.resident-bio {
  color: #b3b3b3;
  line-height: 1.7;
  margin-bottom: 25px;
  text-align: center;
  font-size: 0.95rem;
}

.resident-specialties {
  margin-bottom: 30px;
}

.resident-specialties h4 {
  color: #ffffff;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
}

.specialties-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
}

.specialty {
  background: rgba(255, 7, 58, 0.1);
  color: #ff073a;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid rgba(255, 7, 58, 0.2);
  transition: all 0.3s ease;
}

.specialty:hover {
  background: rgba(255, 7, 58, 0.2);
  transform: translateY(-1px);
}

.soundcloud-section {
  margin-bottom: 25px;
}

.soundcloud-section h4 {
  color: #ffffff;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
}

.soundcloud-player {
  background: #0a0a12;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #2a2a35;
  transition: all 0.3s ease;
}

.soundcloud-player:hover {
  border-color: #ff073a;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.resident-links {
  display: flex;
  justify-content: center;
  gap: 15px;
  padding-top: 20px;
  border-top: 1px solid #2a2a35;
}

.resident-links .social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background: #2a2a35;
  border: 1px solid #3a3a45;
  border-radius: 8px;
  color: #b3b3b3;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  text-decoration: none;
}

.resident-links .social-link:hover {
  background: #ff073a;
  border-color: #ff073a;
  color: white;
  transform: translateY(-2px);
}

/* Responsive */
@media (max-width: 968px) {
  .residents-grid {
    grid-template-columns: 1fr;
    gap: 40px;
    max-width: 500px;
  }
}

@media (max-width: 768px) {
  .residents-section {
    padding: 80px 0;
  }
  
  .residents-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .resident-content {
    padding: 25px;
  }
  
  .resident-image {
    height: 240px;
  }
  
  .resident-stats {
    gap: 20px;
  }
  
  .resident-name {
    font-size: 1.6rem;
  }
  
  .soundcloud-player iframe {
    height: 250px;
  }
}

@media (max-width: 480px) {
  .residents-grid {
    grid-template-columns: 1fr;
  }
  
  .resident-content {
    padding: 20px;
  }
  
  .resident-image {
    height: 200px;
  }
  
  .resident-stats {
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }
  
  .soundcloud-player iframe {
    height: 200px;
  }
}