/* Events Section */
.events-section {
  position: relative;
  background: #0a0a12;
  overflow: hidden;
}

/* Hero Section */
.events-hero {
  position: relative;
  height: 60vh;
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a12 0%, #1a1a2e 50%, #0a0a12 100%);
  overflow: hidden;
  border-radius: 20px;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-glow-1 {
  position: absolute;
  top: 20%;
  left: 15%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(255, 7, 58, 0.2) 0%, transparent 70%);
  filter: blur(50px);
}

.hero-glow-2 {
  position: absolute;
  bottom: 20%;
  right: 15%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(138, 43, 226, 0.15) 0%, transparent 70%);
  filter: blur(40px);
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: #ff073a;
  border-radius: 50%;
  bottom: 0;
}

.hero-content {
  text-align: center;
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.hero-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 4.5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 20px;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.gradient-text {
  background: linear-gradient(45deg, #ff073a, #ff073a, #ff073a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 200% 200%;
  animation: gradient-shift 3s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.hero-subtitle {
  font-size: 1.3rem;
  color: #b3b3b3;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto 40px;
  font-weight: 300;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 60px;
}

.stat {
  text-align: center;
}

.stat-number {
  font-size: 2.2rem;
  font-weight: 800;
  color: #ff073a;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

/* Events Container */
.events-container {
  padding: 100px 0;
  background: #0a0a12;
}

.events-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 50px;
  margin-bottom: 80px;
}

/* Event Card */
.event-card {
  background: linear-gradient(135deg, #1a1a24, #161622);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  overflow: hidden;
  position: relative;
  transition: all 0.4s ease;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.event-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 7, 58, 0.05) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.4s ease;
  border-radius: 25px;
}

.event-card:hover::before {
  opacity: 1;
}

/* Event Header */
.event-header {
  position: relative;
  overflow: hidden;
}

.event-flyer {
  height: 350px;
  overflow: hidden;
  position: relative;
}

.event-flyer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.event-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent, rgba(10, 10, 18, 0.3));
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 25px;
}

.event-date {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  padding: 15px 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(255, 7, 58, 0.4);
  min-width: 80px;
}

.date-day {
  display: block;
  font-size: 1.8rem;
  font-weight: 800;
  line-height: 1;
}

.date-month {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 5px;
}

.event-badge {
  background: rgba(255, 255, 255, 0.95);
  color: #ff073a;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  gap: 8px;
  backdrop-filter: blur(10px);
}

/* Event Content */
.event-content {
  padding: 35px;
}

.event-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
  line-height: 1.3;
}

.event-location {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ff073a;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 25px;
}

.event-location i {
  font-size: 1.1rem;
}

/* DJs Section */
.djs-section {
  margin-bottom: 30px;
}

.djs-title {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 20px;
}

.djs-title i {
  color: #ff073a;
}

.djs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 12px;
}

.dj-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 15px 10px;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
}

.dj-card:hover {
  background: rgba(255, 7, 58, 0.1);
  border-color: rgba(255, 7, 58, 0.3);
}

.dj-avatar {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #2a2a35, #1a1a24);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 8px;
  font-size: 1rem;
  color: #ff073a;
  border: 2px solid rgba(255, 255, 255, 0.1);
}

.dj-name {
  font-size: 0.8rem;
  font-weight: 600;
  color: #ffffff;
  display: block;
}

/* Event Details */
.event-details {
  margin: 25px 0;
  padding: 25px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.info-section,
.pricing-section {
  margin-bottom: 25px;
}

.info-section:last-child,
.pricing-section:last-child {
  margin-bottom: 0;
}

.info-title,
.pricing-title {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 15px;
}

.info-title i,
.pricing-title i {
  color: #ff073a;
}

.event-info {
  color: #b3b3b3;
  line-height: 1.6;
  font-size: 0.95rem;
}

.pricing-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px;
}

.price-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 15px;
  text-align: center;
  transition: all 0.3s ease;
}

.price-item.highlight {
  background: rgba(255, 7, 58, 0.1);
  border-color: rgba(255, 7, 58, 0.3);
}

.price-type {
  display: block;
  font-size: 0.8rem;
  color: #b3b3b3;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.price-amount {
  display: block;
  font-size: 1.2rem;
  font-weight: 700;
  color: #ffffff;
}

/* Event Actions */
.event-actions {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 15px;
  margin-bottom: 25px;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 15px 20px;
  border-radius: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn-primary {
  background: linear-gradient(135deg, #ff073a, #ff073a);
  color: white;
  position: relative;
}

.btn-glow {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #ff073a, #ff073a);
  border-radius: 12px;
  z-index: -1;
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-outline {
  background: transparent;
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.btn-instagram {
  background: linear-gradient(45deg, #E1306C, #F77737);
  color: white;
}

/* Event Countdown */
.event-countdown {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 20px;
}

.countdown-item {
  text-align: center;
}

.countdown-number {
  display: block;
  font-size: 2rem;
  font-weight: 800;
  color: #ff073a;
  line-height: 1;
}

.countdown-label {
  display: block;
  font-size: 0.7rem;
  color: #b3b3b3;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 5px;
}

.countdown-separator {
  color: #ff073a;
  font-size: 1.5rem;
  font-weight: 700;
  margin-top: -10px;
}

/* Card Decoration */
.card-decoration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.decoration-corner {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 7, 58, 0.3);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.event-card:hover .decoration-corner {
  opacity: 1;
}

.corner-1 {
  top: 15px;
  left: 15px;
  border-right: none;
  border-bottom: none;
}

.corner-2 {
  top: 15px;
  right: 15px;
  border-left: none;
  border-bottom: none;
}

.corner-3 {
  bottom: 15px;
  left: 15px;
  border-right: none;
  border-top: none;
}

.corner-4 {
  bottom: 15px;
  right: 15px;
  border-left: none;
  border-top: none;
}

/* Events CTA */
.events-cta {
  text-align: center;
  padding: 60px 0;
  background: linear-gradient(135deg, #1a1a24, #161622);
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.cta-title {
  font-family: 'Exo 2', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 15px;
}

.cta-subtitle {
  font-size: 1.1rem;
  color: #b3b3b3;
  margin-bottom: 40px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

/* Responsive */
@media (max-width: 1024px) {
  .hero-title {
    font-size: 3.5rem;
  }
  
  .events-grid {
    grid-template-columns: 1fr;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 768px) {
  .events-hero {
    height: 50vh;
    min-height: 400px;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .hero-stats {
    gap: 30px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  .event-actions {
    grid-template-columns: 1fr;
  }
  
  .event-flyer {
    height: 280px;
  }
  
  .event-content {
    padding: 25px;
  }
  
  .djs-grid {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .cta-title {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .events-grid {
    grid-template-columns: 1fr;
  }
  
  .event-flyer {
    height: 250px;
  }
  
  .event-overlay {
    padding: 15px;
  }
  
  .event-date {
    padding: 12px 15px;
    min-width: 70px;
  }
  
  .date-day {
    font-size: 1.5rem;
  }
  
  .event-badge {
    font-size: 0.7rem;
    padding: 8px 12px;
  }
}

